<template>
  <div>
    <v-card class="elevation-12">
      <v-card-text
        ><v-form ref="registerForm" v-model="valid" lazy-validation>
          <v-text-field
            prepend-icon="mdi-account-arrow-left"
            v-model.trim="register.firstName"
            :rules="[rules.required, rules.noSpacesOrSpecial]"
            label=""
            placeholder=""
            required
          ></v-text-field>
          <v-text-field
            prepend-icon="mdi-account-arrow-right"
            v-model.trim="register.lastName"
            :rules="[rules.required, rules.noSpacesOrSpecial]"
            label=""
            placeholder=""
            required
          ></v-text-field>
          <v-text-field
            prepend-icon="mdi-email"
            v-model.trim="register.email"
            :rules="[rules.required, rules.email]"
            label=""
            placeholder="email"
            required
          ></v-text-field>
          <v-text-field
            :type="showPassword ? 'text' : 'password'"
            prepend-icon="mdi-lock"
            v-model.trim="register.password"
            :rules="[
              rules.required,
              rules.hasUpperCase,
              rules.hasLowerCase,
              rules.min,
              rules.noSpacesOrSpecial,
            ]"
            label=""
            placeholder=""
            required
          >
            <template v-slot:append>
              <v-icon
                @mousedown="togglePasswordVisibility(true)"
                @mouseup="togglePasswordVisibility(false)"
              >
                {{ showPassword ? "mdi-eye" : "mdi-eye-off" }}
              </v-icon>
            </template>
          </v-text-field>
          <v-text-field
            :type="showconfirmPassword ? 'text' : 'password'"
            prepend-icon="mdi-lock-check"
            v-model.trim="register.confirmPassword"
            :rules="[rules.required, passwordMatch]"
            label=""
            placeholder=""
            required
          >
            <template v-slot:append>
              <v-icon
                @mousedown="toggleConfirmPasswordVisibility(true)"
                @mouseup="toggleConfirmPasswordVisibility(false)"
              >
                {{ showconfirmPassword ? "mdi-eye" : "mdi-eye-off" }}
              </v-icon>
            </template>
          </v-text-field>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="success" @click.prevent="registerSubmit"></v-btn>
          </v-card-actions>
        </v-form></v-card-text
      >
    </v-card>
  </div>
</template>

<script>
import Toast from "@/alert/Toast";
import { auth, db } from "@/methods/firebase";
import { doc, setDoc } from "firebase/firestore";
import {
  createUserWithEmailAndPassword,
  sendEmailVerification,
  updateProfile,
} from "firebase/auth";
export default {
  data() {
    return {
      valid: false, // form
      register: {
        firstName: "",
        lastName: "",
        email: "",
        password: "",
        confirmPassword: "",
      },
      showPassword: false,
      showconfirmPassword: false,
      rules: {
        noSpacesOrSpecial: (value) => {
          const pattern = /^[^\s!@#$%^&*(),.?":{}|<>]*$/; // Regular expression to disallow spaces and special characters
          return pattern.test(value) || "";
        },
        required: (value) => !!value || "",
        email: (value) => {
          const pattern = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/;
          return pattern.test(value) || "";
        },
        min: (value) => value.length >= 6 || "6",
        max: (value) => value.length <= 25 || "25",
        hasUpperCase: (value) => /[A-Z]/.test(value) || "",
        hasLowerCase: (value) => /[a-z]/.test(value) || "",
      },
    };
  },
  methods: {
    // 
    togglePasswordVisibility() {
      this.showPassword = !this.showPassword;
    },
    toggleConfirmPasswordVisibility() {
      this.showconfirmPassword = !this.showconfirmPassword;
    },
    // 
    passwordMatch(value) {
      return value === this.register.password || "";
    },
    registerSubmit() {
      this.isLoading = true;
      this.isRegistering = true;
      this.$refs.registerForm.validate().then((res) => {
        if (res.valid === false) {
          Toast.fire({
            icon: "error",
            title: "",
          });
        } else {
          // Firebase API auth 
          createUserWithEmailAndPassword(auth, this.register.email, this.register.password)
            .then((res) => {
              const user = res.user;
              updateProfile(user, {
                displayName: this.register.firstName + " " + this.register.lastName,
              }).then(() => {
                // 
                auth.languageCode = "zh-TW";
                // Firebase API  auth.currentUser
                sendEmailVerification(auth.currentUser).then(() => {
                  Toast.fire({
                    icon: "success",
                    title: "",
                  });
                });
              });
              this.addUserInfoWithCustomId(user.uid);
              this.register = {
                email: "",
                password: "",
                firstName: "",
                lastName: "",
                confirmPassword: "",
              };
              this.$refs.registerForm.reset();
              this.$emit("switchTab", 0);
            })
            .catch((error) => {
              console.log(`${error.message}`);
              switch (error.message) {
                case "Firebase: Error (auth/email-already-in-use).":
                  Toast.fire({
                    icon: "error",
                    title: "",
                  });
                  break;
                default:
                  Toast.fire({
                    icon: "error",
                    title: "",
                  });
                  break;
              }
            });
        }
      });
      this.isLoading = false;
    },
    //  uid  userInfo 
    async addUserInfoWithCustomId(userId) {
      try {
        //  ID
        const userDoc = doc(db, "userInfo", userId);

        // 
        await setDoc(userDoc, {
          address: "",
          phoneNumber: "",
          photoURL: null,
          orders: [],
          cartItem: [],
          favorite: [],
        });
      } catch (error) {
        console.error("Error adding document: ", error);
      }
    },
  },
};
</script>

<style></style>
